import React from 'react';  // React з папки node_modules
import Preloader from '../../common/Preloader/Preloader';
import s from './ProfileInfo.module.css';
// import ProfileStatus from './ProfileStatus';
import ProfileStatusWithHooks from './ProfileStatusWithHooks';
import userPhoto from "../../../assets/images/user_logo.png";
import arrow_download from "../../../assets/images/arrow_download.svg";




const ProfileInfo = (props) => {
  if (!props.profile) {
    return <Preloader />
  }
  // debugger;

  const onMainPhotoSelected = (e) => {
    if (e.target.files.length) {
      props.savePhoto(e.target.files[0]);
    }
  }
  return (

    <div>
      {/* <div className={s.content_img}>
        <img src='https://img.freepik.com/free-photo/female-hands-counting-money-on-dollar_79075-11663.jpg'>
        </img>
      </div> */}
      <div className={s.descroption_block}>
        {/* <img src={props.profile.photos.large} alt="" /> */}
        {/* <div> */}
        <img src={props.profile.photos.large || userPhoto} className={s.mainPhoto} alt="" />

        {/* </div> */}
        {/* {props.isOwner && <input type={"file"} onChange={onMainPhotoSelected} />} */}
        {props.isOwner &&
          <div className={s.input__wrapper}>
            <input name="file" type={"file"} onChange={onMainPhotoSelected} id="input__file" className={s.input + ' ' + s.input__file} multiple />
            <label for="input__file" className={s.input__file_button}>
              <span className={s.input__file_icon_wrapper}>
                <img className={s.input__file_icon} src={arrow_download} alt="" width="25" /></span>
              <span className={s.input__file_button_text}>Виберіть фото</span>
            </label>
          </div>
        }



        {/* <ProfileStatus status={props.status} updateStatus={props.updateStatus} /> */}

      </div>
      <div>
        <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus} />
        <p>Шукаю робoту:<span className={s.content_text}>{props.profile.lookingForAJob ? "Yes" : "Not"}</span></p>
        <div>Моє ім'я:<span className={s.content_text}>{props.profile.fullName}</span></div>
        <div>Моє id:<span className={s.content_text}>{props.profile.userId}</span></div>

        <div>Iнформація про мене:<span className={s.content_text}>{props.profile.aboutMe}</span></div>
        <div><img src={props.profile.photos.small} alt="" /></div>
      </div>

    </div>


  );
}

export default ProfileInfo;